.tgl {
	display: none;
}
.tgl + .tgl-btn {
	outline: 0;
	display: block;
	width: 80%;
	height: 50px;
	position: relative;
	cursor: pointer;
	user-select: none;
	margin: 5px auto;
	padding: 0.5%;
}
.tgl + .tgl-btn:after,
.tgl + .tgl-btn:before {
	position: relative;
	display: block;
	content: '';
	width: 50%;
	height: 100%;
}
.tgl + .tgl-btn:after {
	left: 0;
}
.tgl + .tgl-btn:before {
	display: none;
}
.tgl:checked + .tgl-btn:after {
	left: 50%;
}
.tgl-skewed + .tgl-btn {
	overflow: hidden;
	transform: skew(-10deg);
	backface-visibility: hidden;
	transition: all 0.2s ease;
	background: #888;
}
.tgl-skewed + .tgl-btn:after,
.tgl-skewed + .tgl-btn:before {
	transform: skew(10deg);
	display: inline-block;
	transition: all 0.2s ease;
	width: 100%;
	font-size: 250%;
	text-align: center;
	position: absolute;
	font-weight: bold;
	color: #fff;
	text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4);
}
.tgl-skewed + .tgl-btn:after {
	left: 100%;
	content: attr(data-tg-on);
}
.tgl-skewed + .tgl-btn:before {
	left: 0;
	content: attr(data-tg-off);
}
.tgl-skewed + .tgl-btn:active {
	background: #888;
}
.tgl-skewed + .tgl-btn:active:before {
	left: -10%;
}
.tgl-skewed:checked + .tgl-btn {
	background: #86d993;
}
.tgl-skewed:checked + .tgl-btn:before {
	left: -100%;
}
.tgl-skewed:checked + .tgl-btn:after {
	left: 0;
}
.tgl-skewed:checked + .tgl-btn:active:after {
	left: 10%;
}
