header {
	font-family: 'koverwatch', 'bignoodletoo';
	width: 100%;
	text-align: left;
	font-weight: bold;

	background: var(--highlight-yellow);
	padding: 10px 0 5px 60px;
}

header h1 {
	font-size: 60px;
	font-weight: normal;
	margin-bottom: 10px;
}

@media screen and (max-width: 400px) {
	header {
		padding: 10px 0 5px 0;
		text-align: center;
	}

	header h1 {
		margin: 0 auto 10px auto;
	}
}
