* {
	box-sizing: border-box;
	margin: 0;
}

html {
	height: 100%;
	width: 100%;
}

body,
#root {
	min-height: 100%;
	width: 100%;
}

main {
	min-height: 565px;
	align-items: center;
	padding: 0;

	text-align: center;
	background-image: url('../../assets/images/background.jpg');
	background-repeat: no-repeat;
	background-size: cover;
}

.App {
	--button-blue: rgb(70, 138, 194);
	--button-yellow: rgb(221, 155, 42);
	--highlight-yellow: rgb(251, 206, 82);
	--logo-silver: rgb(212, 213, 215);
	--menu-card: rgb(240, 240, 234);

	width: 100%;
	height: 100%;
}

.overwatch-button-primary {
	font-family: 'buttons', 'bignoodletoo';
	display: block;
	margin: auto;
	width: fit-content;
	color: white;
	text-decoration: none;
	font-weight: bold;
	border: 3px solid transparent;
	background-color: #ffd200;
	box-shadow: 0px 0px 3px #ffd200;
	border-radius: 5px;
	opacity: 0.8;
	text-transform: uppercase;
	text-shadow: 0px 0px 1px white;
	transition: opacity 0.2s ease-out, border 0.2s ease-out;
}
.overwatch-button-primary:hover {
	opacity: 1;
	border: 3px solid white;
}

.overwatch-button-secondary {
	display: inline-block;
	min-width: 150px;
	line-height: 65px;
	padding: 0px 3px;
	border: 3px solid transparent;
	background-color: #469ed7;
	box-shadow: 0px 0px 3px #469ed7;
	border-radius: 5px;
	-moz-appearance: none;
	-webkit-appearance: none;
	opacity: 0.8;
	font-family: 'buttons', 'bignoodletoo';
	text-transform: uppercase;
	font-size: 26px;
	color: white;
	text-shadow: 0px 0px 1px white;
	transition: opacity 0.2s ease-out, border 0.2s ease-out;
}
.overwatch-button-secondary:hover {
	opacity: 1;
	border: 3px solid white;
}

@media screen and (max-width: 450px) {
	main {
		min-height: 700px;
	}
}
